.sample {
  text-align: left;
  background-color: #f2f2f2;
  color: @black1;
}

@media (min-width: @tablet-width) {
  .sample {
    background-color: white;
  }
}

@media (min-width: @desktop-width) {
  .sample {
    display: flex;
    background-color: #eaeaea;
  }
}

.sample__title {
  margin: 0;
  margin-bottom: 25px;
  padding-top: 20px;
  font-size: 36px;
  line-height: 1.67;
  color: @black1;
}

@media (min-width: @tablet-width) {
  .sample__title {
    margin-bottom: 65px;
    padding: 0;
    font-size: 60px;
    line-height: 1;
  }

  .sample__wrapper {    
    margin-bottom: 20px;
  }
}

@media (min-width: @desktop-width) {
  .sample__title {
    margin-bottom: 66px;
  }
}

@media (min-width: @tablet-width) {
  .sample__container {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: @desktop-width) {
  .sample__container {
    flex-direction: column;
    background-color: #eaeaea;
  }
}

.sample__list {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  justify-content: space-between;
  width: 280px;
  list-style: none;
}

@media (min-width: @tablet-width) {
  .sample__list {
    min-width: 366px;
    margin-bottom: 250px;
  }
}

@media (min-width: @desktop-width) {
  .sample__list {
    margin-bottom: 20px;
  }
}

.sample__description {
  margin: 0;
  margin-bottom: 24px;
  padding: 0 10px 0 0;
  font-family: @arial;
  font-size: 14px;
  line-height: 1.29;
  color: #444444;
}

@media (min-width: @tablet-width) {
  .sample__description {
    margin-bottom: 56px;
    font-size: 16px;
    line-height: 1.5;
  }
}

@media (min-width: @desktop-width) {
  .sample__description {
    margin-bottom: 71px;
    padding: 76px 30px 0 0;
  }
}

.sample__item {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 124px;
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
}

@media (min-width: @tablet-width) {
  .sample__item {
    width: 162px;
  }
}

.sample__value {
  display: block;
  padding: 5px 0 15px;
  margin: 0;
  font-family: @oswald;
  font-size: 24px;
}

@media (min-width: @tablet-width) {
  .sample__value {
    padding-top: 15px;
    font-size: 30px;
    line-height: 1.23;
  }
}

.sample__field {
  content: "";
  margin-top: 45px;
  left: 22px;
  position: absolute;
  width: 80px;
  display: block;
  font-family: @arial;
  font-size: 12px;
  text-transform: lowercase;
  background-color: #f2f2f2;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .sample__field {
    left: 0;
    margin-top: 60px;
    width: 160px;
    font-size: 14px;
    background-color: white;
  }
}

@media (min-width: @desktop-width) {
  .sample__field {
    background-color: inherit;
  }
}

.sample__costs {
  margin-bottom: 44px;
  font-size: 14px;
  line-height: 1.43px;
  text-transform: uppercase;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .sample__costs {    
    width: 150px;
    min-height: 50px;
    margin: 0;
    padding: 0;
    padding-top: 20px;
    font-size: 20px;
  }
}

@media (min-width: @desktop-width) {
  .sample__costs {
    padding-top: 49px;
    padding-bottom: 75px;
    text-align: left;
  }
}

.sample__auxiliary {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: @tablet-width) {
  .sample__auxiliary {   
    justify-content: flex-end;
    height: 460px;
    background-color: #eaeaea;
  }
}

@media (min-width: @desktop-width) {
  .sample__auxiliary {
    flex-basis: 50%;
    margin-top: 130px;
  }
}

@media (min-width: @tablet-width) {
  .sample__picture {
    position: relative;    
    width: 636px;
    height: 517px;    
    overflow: hidden;
  }
}

@media (min-width: @desktop-width) {
  .sample__wrapper {
    flex-basis: 50%;
  }
}  

 @media (min-width: @tablet-width) {
    .sample__picture-wrapper {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 0;
    bottom: 0;
    width: 50%;
    height: 517px;
    overflow: hidden;
    background: linear-gradient(to bottom, #ffffff 33%, #eaeaea 33%, #eaeaea 100%);

    &--before {
      position: absolute;
      top: -190px;
      left: -10px;
      z-index: 2;
      background: linear-gradient(to bottom, #ffffff 33%, #f2f2f2 33%, #f2f2f2 100%);
      width: 50%;
    }

    &--after {
      position: absolute;
      top: -190px;
      right: 0;
      z-index: 2;
      background: linear-gradient(to bottom, #ffffff 33%, #f2f2f2 33%, #f2f2f2 100%);
      width: 50%;
    }
  }
}

