      }    
    }
}*/

.sample__picture-image {
  display: block;
  margin-bottom: 35px;
}

.sample__picture-image--after {
  display: none;
}

@media (min-width: @tablet-width) {
  .sample__picture-image {
    position: relative;
    width: 690px;
    max-width: none;
    height: 517px;
    

  &--before {
      top: 0;
      left: -10px;
      z-index: 6;
          
    }

    &--after {        
        right: -5px;
        top: 0;
        z-index: 5;
    }
  }
}

@media (min-width: @desktop-width) {
  .sample__picture-image {
    position: absolute;
    top: 1620px;
    left: 680px;
  }
}

.sample__controls {
  display: flex;
  justify-content: space-between;
  padding-bottom: 35px;
  margin: 0 auto;
  width: 203px;
}

@media (min-width: @tablet-width) {
  .sample__controls {
    padding-bottom: 81px;
    width: 78%;
    font-size: 20px;
  }
}

@media (min-width: @desktop-width) {
  .sample__controls {
    margin: 495px 0 0;
    padding-bottom: 65px;
    padding-left: 35px;
    width: 80%;
  }
}

.sample__controls-before {
  text-transform: uppercase;
  padding-left: 5px;
}

.sample__controls-after {
  padding-left: 5px;
  text-transform: uppercase;
}

.sample__controls-slider {
  position: relative;
  box-sizing: border-box;
  width: 84px;
  height: 14px;
  margin-top: 3px;
  background-color: #ffffff;
  border: 1px solid #cdcdcd;
  border-radius: 10px;
}

.sample__controls-slider::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 5px;
  width: 35px;
  height: 6px;
  background-color: #68b738;
  border-radius: 10px;
}

@media (min-width: @tablet-width) {
  .sample__controls-slider {
    width: 428px;
    height: 6px;
  }
}
